import { PressEvent } from '@react-types/shared';
import { AnimatePresence, motion } from 'framer-motion';
import React, { RefAttributes } from 'react';
import { useFocusRing, useHover, usePress } from 'react-aria';
import { transitions } from '../../../hooks/transitions';
import { clsx } from '../../../libs/clsx';
import { mergeProps } from '../../../libs/merge-props';
import { Icon, IconColorProvider, MonochromeIconDefinition } from '../../icons';
import { Spacer } from '../../layout';
import { InverseThemeSemantics, useTheme } from '../../theme-provider';

import { createGenericForwardRefsFactory } from '../../../libs/generic-forward-refs';
import { BaseColorName, ColorTone } from '../../../libs/theme';

import styles from './text-button.less';

interface TextButtonProps extends RefAttributes<HTMLAnchorElement | HTMLButtonElement> {
  disabled?: boolean;
  onPress?: (event: PressEvent) => void;
  size?: 'sm' | 'md';
  color?: 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'error';
  leadingIcon?: MonochromeIconDefinition;
  trailingIcon?: MonochromeIconDefinition;
  className?: string;
}

/**
 * Determines a _default_ color for icons nested in a `<TextButton>`.
 */
function useIconColor(baseColor?: BaseColorName, inverseSemantics?: boolean, isHovered?: boolean, isPressed?: boolean) {
  const theme = useTheme({ inverseSemantics });

  let tone: ColorTone = 'base';

  // ":hover" state
  if (isHovered) {
    tone = theme.isLightTheme ? 'lighter' : 'darker';
  }

  // ":active" state
  if (isPressed) {
    tone = theme.isLightTheme ? 'darkest' : 'lightest';
  }

  return baseColor ? theme.hex[baseColor][tone] : undefined;
}

const createTextButtonComponents = /* @__PURE__ */ createGenericForwardRefsFactory(['button', 'a']);
const TextButtonComponents = /* @__PURE__ */ createTextButtonComponents<TextButtonProps>(
  'TextButton',

  { color: 'primary', size: 'md' },

  (props, externalRef) => {
    const { onPress, className, disabled, size, color, leadingIcon, trailingIcon, children, component, ...otherProps } =
      props;

    const { isFocusVisible, focusProps } = useFocusRing();
    const { isHovered, hoverProps } = useHover({ isDisabled: disabled });
    const { isPressed, pressProps } = usePress({ onPress, isDisabled: disabled });
    const getScaleProps = transitions.useScaleIn();

    const inverseSemantics = color === 'secondary';
    const iconColor = useIconColor(color, inverseSemantics, isHovered, isPressed);

    const content = (
      <>
        <AnimatePresence>
          {isFocusVisible && <motion.span aria-hidden="true" className={styles.focusRing} {...getScaleProps(1.08)} />}
        </AnimatePresence>

        {leadingIcon && (
          <>
            <Icon size={size === 'sm' ? 20 : 24} type={leadingIcon} />
            <Spacer size={8} />
          </>
        )}

        <span className={styles.buttonLabel}>{children}</span>

        {trailingIcon && (
          <>
            <Spacer size={8} />
            <Icon size={size === 'sm' ? 20 : 24} type={trailingIcon} />
          </>
        )}
      </>
    );

    return (
      <IconColorProvider colorA={iconColor}>
        <InverseThemeSemantics active={inverseSemantics}>
          {React.createElement(
            component,

            {
              className: clsx([
                className,
                styles.TextButton,
                styles[size!],
                styles[`size_${size}`],
                styles[`color_${color}`],
                disabled && styles.disabled,
                isPressed && styles.active,
              ]),
              disabled,
              'aria-disabled': disabled,
              ...mergeProps(pressProps, focusProps, hoverProps, otherProps),
              ref: externalRef,
            },

            content,
          )}
        </InverseThemeSemantics>
      </IconColorProvider>
    );
  },
);

export const TextButton = /* @__PURE__ */ Object.assign(TextButtonComponents.button, TextButtonComponents);
